export default {
    props: {
        order: {
            require: true,
        },
        title: {
            type: String,
            require: true,
        },
        githubUrl: {
            type: String,
            require: false,
        },
        demoLink: {
            type: String,
            require: false,
        },
        previewTexts: {
            type: Array,
            require: true,
        },
        thumbnailUrl: {
            type: String,
            require: true,
        },
        tags: {
            type: Array,
            require: true,
        },
    },
    computed: {
        imgUrlStyle() {
            return `background-image: url('${this.thumbnailUrl}')`;
        },
    },
};
